import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Card,
  CardHeader,
  CardContent,
  IconButton,
  useMediaQuery,
  Tooltip,
  Theme,
  createStyles, 
  Avatar
} from '@material-ui/core';
import ReactApexChart from 'react-apexcharts';
import GetAppIcon from '@material-ui/icons/GetApp';
import html2canvas from 'html2canvas';
import { saveAs } from 'file-saver';
import AssessmentIcon from '@material-ui/icons/Assessment';

const options = {
  chart: {
    toolbar: {
      show: false,
    },
    zoom: {
      enabled: false,
    },
  },
  plotOptions: {
    bar: {
      horizontal: false,
      columnWidth: '100%',
      columnHeight: '100%',
      distributed: true,
    },
  },
  dataLabels: {
    enabled: false,
  },
  markers: {
    size: 2,
  },
  stroke: {
    curve: 'smooth',
    lineCap: 'round',
  },
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      borderRadius: 4,
    },
    content: {
      paddingTop: '6px',
      padding: 16,
    },
    cardActionContainer: {
      display: 'flex',
      justifyItems: 'center',
      alignItems: 'flex-end',
    },
    header: {
      backgroundColor: theme.palette.primary.main,
      height: '64px',
    },
    title: {
      color: theme.palette.common.white,
      fontSize: '1.25rem',
      fontWeight: 500,
      lineHeight: 1.6,
    },
    downloadButton: {
      color: theme.palette.common.white,
    },
    avatar: {
      backgroundColor: theme.palette.common.white,
      color: theme.palette.primary.main,
      width: theme.spacing(3),
      height: theme.spacing(3),
    },
    icon: {
      width: theme.spacing(2),
      height: theme.spacing(2),
    }
  })
);

interface ColumnChart {
  title: string;
  categories: string[];
  series: { name: string; data: number[] }[];
  horizontal: boolean;
  columnColorFormatter?: ({ value }: any) => string;
  height?: number;
  singleSeriesName?: string;
  yAxisFormatter?: (value: number) => string;
  xAxisFormatter?: (value: number) => string;
}

const ColumnChart = ({
  title,
  categories,
  series,
  horizontal,
  columnColorFormatter,
  yAxisFormatter,
  xAxisFormatter,
  height,
  singleSeriesName,
}: ColumnChart) => {
  const classes = useStyles();
  const systemTheme = useMediaQuery('(prefers-color-scheme: dark)') ? 'dark' : 'light';

  const downloadElement = () => {
    const element = document.getElementById(`column-chart-${title}`);
    element &&
      html2canvas(element).then(function (canvas) {
        canvas.toBlob(function (blob) {
          blob && saveAs(blob, `${title}.png`);
        });
      });
  };

  const DownloadButton = () => (
    <Tooltip title="Descargar im√°gen">
      <IconButton onClick={downloadElement} className={classes.downloadButton}>
        <GetAppIcon />
      </IconButton>
    </Tooltip>
  );

  return (
    <Card className={classes.root}>
      <CardHeader
        className={classes.header}
        classes={{ title: classes.title }}
        title={title}
        action={<DownloadButton />}
        avatar={
          <Avatar aria-label="time" className={classes.avatar}>
            <AssessmentIcon className={classes.icon} />
          </Avatar>
        }
      />
      <CardContent style={{ paddingTop: 0 }} id={`column-chart-${title}`}>
        <ReactApexChart
          options={{
            ...options,
            theme: { mode: systemTheme },
            xaxis: {
              categories,
              labels: { formatter: xAxisFormatter },
            },
            fill: {
              colors: columnColorFormatter ? [columnColorFormatter] : undefined,
            },
            yaxis: {
              labels: {
                formatter: yAxisFormatter,
              },
            },
            plotOptions: {
              bar: {
                horizontal: horizontal,
              },
            },
            tooltip: {
              y: {
                title: {
                  formatter: singleSeriesName ? () => singleSeriesName : undefined,
                },
                formatter: xAxisFormatter,
              },
            },
          }}
          series={series}
          type="bar"
          height={height || 400}
        />
      </CardContent>
    </Card>
  );
};

export default ColumnChart;
