import { Grid } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import ColumnChart from '../chart-card';
import TimesCard from './time-card';

const Profiles = () => {
  const [processTimes, setProcessTimes] = useState<{time: number; tasks: number}[]>([]);
  const [numberOfProcessors, setNumberOfProcessors] = useState(0);
  const [hardwareProfile, setHardwareProfile] = useState<number[]>([]);

  useEffect(() => {
    if (numberOfProcessors > 0) {
      const hardwareProfile: number[] = [];
      processTimes.forEach(item => {
        let value = item.tasks
        while (value > numberOfProcessors) {
          hardwareProfile.push(numberOfProcessors);
          value -= numberOfProcessors;
        }
        hardwareProfile.push(value);
      })
      setHardwareProfile(hardwareProfile)
    }
  }, [processTimes, numberOfProcessors]);

  return (
    <Grid container spacing={1}>
      <Grid item xs={12} sm={3}>
        <TimesCard 
          times={processTimes} 
          setTimes={setProcessTimes} 
          processors={numberOfProcessors}
          setProcessors={setNumberOfProcessors}
        />
      </Grid>
      <Grid item xs={12} sm={9} container direction="column" spacing={1}>
        <Grid item xs={12}>
          <ColumnChart 
            categories={processTimes.map(t => `Tiempo ${t.time}`)} 
            title="Perfil de Software" 
            series={[{name: 'Tareas', data: processTimes.map(t => t.tasks)}]} 
            horizontal={false}
            yAxisFormatter={(value: number) => value.toFixed(0)}
          />
        </Grid>
        <Grid item xs={12}>
          <ColumnChart 
            categories={hardwareProfile.map((_t, index) => `Tiempo ${index + 1}`)} 
            title="Perfil de Hardware" 
            series={[{name: 'Tareas', data: hardwareProfile}]} 
            horizontal={false} 
            yAxisFormatter={(value: number) => value.toFixed(0)}
          />
        </Grid>
      </Grid>
    </Grid>
  )
};

export default Profiles;
