import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button, TextField } from '@material-ui/core';
import React, { useEffect, useState } from 'react';

interface ProcessorDialog {
  open: boolean;
  setOpen: (value: boolean) => void;
  processors: number;
  setProcessors: Function;
}

const ProcessorDialog = ({open, setOpen, processors, setProcessors}: ProcessorDialog) => {
  const [input, setInput] = useState<string>(processors.toString());
  const [error, setError] = useState(false);
  const handleClose = () => {
    setOpen(false);
  }

  useEffect(() => {
    if (input === '' || parseInt(input) <= 0) setError(true);
    else setError(false);
  }, [input])

  const handleSetProcessors = () => {
    setProcessors(parseInt(input));
    handleClose();
  }

  return (
    <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{"Establecer procesadores"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Ingres√° la cantidad de procesadores.
          </DialogContentText>
          <TextField 
            fullWidth 
            error={error}
            helperText={error && 'Algo ten√©s que ingresar üò¨'}
            type="number"
            value={input || ''}
            label="Procesadores" 
            onChange={(event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => setInput(event.target.value)} 
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            Cancelar
          </Button>
          <Button onClick={handleSetProcessors} color="primary" autoFocus disabled={error}>
            Aplicar
          </Button>
        </DialogActions>
      </Dialog>
  )
}

export default ProcessorDialog;
