import React, { Dispatch, useEffect, useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import AddIcon from '@material-ui/icons/Add';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import TimelineIcon from '@material-ui/icons/Timeline';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import RotateLeftIcon from '@material-ui/icons/RotateLeft';
import { Button, Collapse, InputAdornment, List, ListItem, ListItemSecondaryAction, ListItemText, OutlinedInput, TextField, Tooltip } from '@material-ui/core';
import ProcessorDialog from '../../dialogs/set-processors';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      // maxWidth: 345,
    },
    media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    header: {
      backgroundColor: theme.palette.primary.main,
      height: '64px',
    },
    title: {
      color: theme.palette.common.white,
      fontSize: '1.25rem',
      fontWeight: 500,
      lineHeight: 1.6,
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: theme.palette.common.white,
      color: theme.palette.primary.main,
      width: theme.spacing(3),
      height: theme.spacing(3),
    },
    icon: {
      width: theme.spacing(2),
      height: theme.spacing(2),
    }
  }),
);

interface TimesCard {
  times: {time: number; tasks: number}[];
  setTimes: Dispatch<React.SetStateAction<{
    time: number;
    tasks: number;
  }[]>>;
  processors: number;
  setProcessors: Dispatch<React.SetStateAction<number>>;
}

export default function TimesCard({ times, setTimes, processors, setProcessors }: TimesCard) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [newTimeTasks, setNewTask] = useState('0');
  const [open, setOpen] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    if (newTimeTasks === '' || parseInt(newTimeTasks) < 0) setError(true);
    else setError(false);
  }, [newTimeTasks]);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const handleAddTime = () => {
    setTimes(t => [...t, { time: t.length + 1, tasks: parseInt(newTimeTasks) } ]);
    setNewTask('0');
  }

  return (
    <Card className={classes.root}>
      <CardHeader
        className={classes.header}
        classes={{ title: classes.title }}
        avatar={
          <Avatar aria-label="time" className={classes.avatar}>
            <TimelineIcon className={classes.icon} />
          </Avatar>
        }
        title="Configurar tiempos"
      />
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          A continuaci√≥n podr√°s ingresar los valores del perfil de software para calcular el perfil de hardware correspondiente. Es necesario que ingreses la cantidad de tareas por unidad de tiempo y la cantidad de procesadores.
        </Typography>
        <List dense disablePadding>
          {times.map(item => (
            <Collapse in={Boolean(item)} key={`time-${item.time}`} unmountOnExit>
              <ListItem>
                <ListItemText primary={`Tiempo ${item.time}`} />
                <ListItemSecondaryAction>
                  {`${item.tasks} tareas`}
                </ListItemSecondaryAction>
              </ListItem>
            </Collapse>
          ))}
          <ListItem>
            <ListItemText primary="Agregar tareas" secondary={`Para el tiempo ${times.length + 1}`} />
            <ListItemSecondaryAction>
            <TextField
              id="outlined-add"
              type="number"
              error={error}
              helperText={error && 'üëéüèº'}
              style={{ maxWidth: 100 }}
              value={newTimeTasks.toString()}
              onChange={(event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => setNewTask(event.target.value)}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="add new time"
                      onClick={handleAddTime}
                      edge="end"
                      disabled={error}
                    >
                    <AddIcon />
                  </IconButton>
                </InputAdornment>
                )
              }}
            />
            </ListItemSecondaryAction>
          </ListItem>
        </List>
      </CardContent>
      <CardActions disableSpacing>
        <Button onClick={() => setOpen(true)}>
          {processors > 0 ? `Procesadores (${processors})` : "Establecer procesadores"}
        </Button>
        <Tooltip title="Reiniciar">
          <IconButton
            className={classes.expand}
            onClick={() => setTimes([])}
            aria-expanded={expanded}
            aria-label="show more"
          >
            <RotateLeftIcon />
          </IconButton>
        </Tooltip>
      </CardActions>
      <ProcessorDialog open={open} setOpen={setOpen} processors={processors} setProcessors={setProcessors} />
    </Card>
  );
}